// import {Helmet} from 'react-helmet'
import React from 'react';
import ReactTooltip from 'react-tooltip';
import { Row, Col } from 'reactstrap';

//import './.scss'
import PhotoshopImg from './Img/my_tools/photoshop.png';
import Mockups3Img from './Img/my_tools/mockups3.png';
import MicrosoftVisioImg from './Img/my_tools/visio.png';
import VsCodeImg from './Img/my_tools/vs-code-insider.png';
import PixelZoomerImg from './Img/my_tools/pixel-zoomer.png';
import GitBashImg from './Img/my_tools/git-bash.png';
import CmderImg from './Img/my_tools/cmder.png';
import ApiTesterImg from './Img/my_tools/api-tester.png';
import PgAdminImg from './Img/my_tools/pg-admin.png';
import MongoAdminImg from './Img/my_tools/admin-mongo.png';
import Robo3tImg from './Img/my_tools/robo3t.png';
import GitImg from './Img/my_tools/git.png';
import ZWampImg from './Img/my_tools/z-wamp.jpeg';

const Tools = (props) => {

  //useEffect
  // React.useEffect(() => {
  //   window.scrollTo(0, 0);
  // }, []);


  return (
    <React.Fragment>
      <section id="Tools">
        <h2>Tools I Use <span className="stroke"></span></h2>

        <Row className="tools-block">

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='photoshopImg' href="https://www.adobe.com/products/photoshop.html" target="_blank" rel="noopener noreferrer">
                <img src={PhotoshopImg} alt="photoshop logo" />
              </a>
              <ReactTooltip id='photoshopImg' place="bottom">
                <span>Photoshop</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='Mockups3Img' href="https://balsamiq.com/wireframes/desktop/" target="_blank" rel="noopener noreferrer">
                <img src={Mockups3Img} alt="photoshop logo" />
              </a>
              <ReactTooltip id='Mockups3Img' place="bottom">
                <span>Balsamiq Wireframes</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='MicrosoftVisioImg' href="https://www.microsoft.com/en-us/store/collections/visio" target="_blank" rel="noopener noreferrer">
                <img src={MicrosoftVisioImg} alt="photoshop logo" />
              </a>
              <ReactTooltip id='MicrosoftVisioImg' place="bottom">
                <span>Microsoft Visio</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='VsCodeImg' href="https://code.visualstudio.com/insiders/" target="_blank" rel="noopener noreferrer">
                <img src={VsCodeImg} alt="VS Code logo" />
              </a>
              <ReactTooltip id='VsCodeImg' place="bottom">
                <span>Microsoft VS Code Insider</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='PixelZoomerImg' href="http://pixelzoomer.com/" target="_blank" rel="noopener noreferrer">
                <img src={PixelZoomerImg} alt="Pixel Zoomer logo" />
              </a>
              <ReactTooltip id='PixelZoomerImg' place="bottom">
                <span>Pixel Zoomer</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='ApiTesterImg' href="https://chrome.google.com/webstore/detail/talend-api-tester-free-ed/aejoelaoggembcahagimdiliamlcdmfm" target="_blank" rel="noopener noreferrer">
                <img src={ApiTesterImg} alt="Talend API Tester logo" />
              </a>
              <ReactTooltip id='ApiTesterImg' place="bottom">
                <span>Talend API Tester</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='GitBashImg' href="https://gitforwindows.org/" target="_blank" rel="noopener noreferrer">
                <img src={GitBashImg} alt="Git Bash logo" />
              </a>
              <ReactTooltip id='GitBashImg' place="bottom">
                <span>Git Bash</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='CmderImg' href="https://cmder.net/" target="_blank" rel="noopener noreferrer">
                <img src={CmderImg} alt="Cmder logo" />
              </a>
              <ReactTooltip id='CmderImg' place="bottom">
                <span>Cmder</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='PgAdminImg' href="https://www.pgadmin.org/" target="_blank" rel="noopener noreferrer">
                <img src={PgAdminImg} alt="pgAdmin logo" />
              </a>
              <ReactTooltip id='PgAdminImg' place="bottom">
                <span>pgAdmin</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='MongoAdminImg' href="https://adminmongo.markmoffat.com/" target="_blank" rel="noopener noreferrer">
                <img src={MongoAdminImg} alt="adminMongo logo" />
              </a>
              <ReactTooltip id='MongoAdminImg' place="bottom">
                <span>adminMongo</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='Robo3tImg' href="https://robomongo.org/" target="_blank" rel="noopener noreferrer">
                <img src={Robo3tImg} alt="Robo 3T logo" />
              </a>
              <ReactTooltip id='Robo3tImg' place="bottom">
                <span>Robo 3T</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='GitImg' href="https://git-scm.com/" target="_blank" rel="noopener noreferrer">
                <img src={GitImg} alt="Git logo" />
              </a>
              <ReactTooltip id='GitImg' place="bottom">
                <span>Git</span>
              </ReactTooltip>
            </div>
          </Col>

          <Col xs="3">
            <div className="tool">
              <a data-tip data-for='ZWampImg' href="http://zwamp.sourceforge.net/" target="_blank" rel="noopener noreferrer">
                <img src={ZWampImg} alt="Z-Wamp logo" />
              </a>
              <ReactTooltip id='ZWampImg' place="bottom">
                <span>Z-Wamp</span>
              </ReactTooltip>
            </div>
          </Col>

        </Row>
      </section>
    </React.Fragment>
  );
};

export default Tools;