// import {Helmet} from 'react-helmet'
import React from 'react';
import { Row, Col } from 'reactstrap';

//import './.scss'

const Skills = (props) => {

  //useEffect
  // React.useEffect(() => {
  //   window.scrollTo(0, 0);
  // }, []);


  return (
    <React.Fragment>
      <section id="Skills">
        <h1>Skills <span className="stroke"></span></h1>

        <Row className="skills-block">

          <Col md="6" className="skills front-end">
            <h3>Frontend:</h3>
            <ul>
              <li>Photoshop</li>
              <li>HTML5</li>
              <li>CSS3</li>
              <li>SASS/ SCSS</li>
              <li>Bootstrap 4</li>
              <li>jQuery </li>
              <li>WordPress <small>&#x28; Theme Developing &#x29;</small> </li>
              <li>JavaScript <small> &#x28; OOP, Modern/ ES6, ES7, ES8 &#x29; </small></li>
              <li>React.js</li>
              <li>Gatsby</li>
              <li>Vue.js</li>
            </ul>
          </Col>

          <Col md="6" className="skills back-end">
            <h3>Backend:</h3>
            <ul>
              <li>Node.js</li>
              <li>PHP</li>
              <li>SQL <small>&#x28; mySQL, PostgresQL &#x29;</small></li>
              <li>noSQL <small>&#x28; MongoDB &#x29;</small></li>
              <li>Web API/ RESTful APIs</li>
            </ul>
          </Col>

          <Col md="6" className="skills others mx-auto">
            <h3>Others:</h3>
            <ul>
              <li>Command Line</li>
              <li>Git</li>
              <li>Version Control</li>
            </ul>
          </Col>
        </Row>
      </section>
    </React.Fragment>
  );
};

export default Skills;